



















import { Component, Vue, Watch } from 'vue-property-decorator';
import Authenticated from '@/layouts/Authenticated.vue';
import LineChart from '@/components/charts/LineChart.vue';
import Entry from '@/components/common/Entry.vue';
import DiaryEntry from '@/models/DiaryEntry';
import Exercise from '@/models/Exercise';
import { DateTime } from 'luxon';

@Component({
    components: {
        Authenticated,
        LineChart,
        Entry,
    },
})
export default class Dashboard extends Vue {
    private entries: DiaryEntry[] = [
        new DiaryEntry(DateTime.local().toISO(), [
            new Exercise('Bicep curls', 10),
            new Exercise('Bench press', 20),
            new Exercise('Incline bench press', 20),
            new Exercise('Decline bench press', 20),
        ]),
        new DiaryEntry(DateTime.local().plus({ days: 1 }).toISO(), [
            new Exercise('Bicep curls', 10),
            new Exercise('Bench press', 30),
            new Exercise('Incline bench press', 20),
            new Exercise('Decline bench press', 20),
        ]),
        new DiaryEntry(DateTime.local().plus({ days: 3 }).toISO(), [
            new Exercise('Bicep curls', 10),
            new Exercise('Bench press', 20),
            new Exercise('Incline bench press', 20),
            new Exercise('Decline bench press', 20),
        ]),
    ];

    private lineData: any = {
        labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
        datasets: [{
            label: 'Benchpress',
            backgroundColor: '#6435c9',
            borderColor: '#6435c9',
            data: [10, 20, 22, 30, 35, 50, 60],
            fill: false,
        },
        {
            label: 'Bicep curls',
            fill: false,
            backgroundColor: '#007bff',
            borderColor: '#007bff',
            data: [5, 10, 11, 12, 14, 15, 18],
        }],
    };


    private lineOptions: any = {
        responsive: true,
         maintainAspectRatio: false,
        title: {
            display: true,
            text: 'Progression in kg',
        },
        scales: {
            yAxes: [{
                ticks: {
                    suggestedMin: 10,
                    suggestedMax: 50,
                },
            }],
        },
    };
}
